import Vue from 'vue';
import Vuex from 'vuex';
import clone from '@/lib/clone';
import createId from '@/lib/createId';
import router from '@/router';

Vue.use(Vuex);// 把store 绑到Vue.prototype.$store = store

const store = new Vuex.Store({
    state: {
        recordList: [],
        createRecordError: null,
        createTagError: null,
        tagList: [],
        currentTag: undefined,
        output: '0',
        selectedTag: '其他',
        type: '-',
        notes: '',
        budget: null,
        payTags: ['其他', '餐饮', '交通', '购物', '服饰', '日用', '娱乐', '食材', '零食', '烟酒', '学习', '医疗', '住房', '水电', '通讯', '红包', '借出'],
        incomeTags: ['其他', '薪资', '奖金', '借入', '收债', '利息', '回收', '投资']
    } as RootState,
    mutations: {
        //budget
        removeBudget(state){
            state.budget=null;
            store.commit('saveBudget');
        },
        fetchBudget(state){
            state.budget =JSON.parse(window.localStorage.getItem('budget') || '0');
        },
        createBudget(state,money: number) {
            state.budget=money;
            store.commit('saveBudget');
        },
        saveBudget(state){
            window.localStorage.setItem('budget',JSON.stringify(state.budget));
        },
        //tag
        updateTag(state, name: string) {
            if (state.payTags.indexOf(name) >= 0 || state.incomeTags.indexOf(name) >= 0) {
                state.selectedTag = name;
            }
        },
        //record
        fetchRecords(state) {
            state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]') as RecordItem[];
        },
        createRecord(state) {
            const record: RecordItem = {
                id: null,
                tag: state.selectedTag,
                notes: state.notes,
                type: state.type,
                amount: parseFloat(state.output)
            };
            const record2: RecordItem = clone(record);
            record2.createdAt = new Date().toISOString();
            record2.id = createId().toString()
            state.recordList.push(record2);
            store.commit('saveRecords');
        },
        saveRecords(state) {
            window.localStorage.setItem('recordList', JSON.stringify(state.recordList));
            state.output = '0'
            state.notes = ''
        },
        updateRecord(state,payload) {
            const {id, notes, amount} = payload;

            const idList = state.recordList.map(item => item.id);
            if (idList.indexOf(id) >= 0) {
                const record = state.recordList.filter(item => item.id === id)[0];
                record.notes = notes;
                record.amount = amount;
                store.commit('saveRecords');
            }
        },
        removeRecord(state,id: string){
            let index = -1;
            for (let i = 0; i < state.recordList.length; i++) {
                if (state.recordList[i].id === id) {
                    index = i;
                    break;
                }
            }
            if(index>=0){
                state.recordList.splice(index, 1);
                store.commit('saveRecords');
                router.back();
            }else {
                window.alert('删除失败');
            }

            store.commit('saveRecords');
        },

        //numberPad
        inputContent(state, event: MouseEvent) {
            const button = (event.target as HTMLButtonElement);
            const input = button.textContent!;
            if (state.output.length === 16) {
                return
            }
            if (state.output === '0') {
                if ('0123456789'.indexOf(input) >= 0) {
                    state.output = input;
                } else {
                    state.output += input;
                }
                return;
            }
            if (state.output.indexOf('.') >= 0 && input === '.') {
                return;
            }
            state.output += input;
        },

        removeInput(state) {
            if (state.output.length === 1) {
                state.output = '0';
            } else {
                state.output = state.output.slice(0, -1);
            }
        },
        clearInput(state) {
            state.output = '0';
        },
        // type
        selectType(state, type: string) {
            if (type !== '-' && type !== '+') {
                throw new Error('type is unknown');
            }
            state.type = type;
            if ((state.type === '+' && state.payTags.indexOf(state.selectedTag) >= 0) || (state.type === '-' && state.incomeTags.indexOf(state.selectedTag) >= 0)) {
                state.selectedTag = '其他';
            }
        }

    }
});
export default store;
