







import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component({
  computed: {
    selectedTag(){
      return this.$store.state.selectedTag
    },
    selectedType(){
      if(this.$store.state.type==='-'){
        return 'payColor'
      }else {
        return 'incomeColor'
      }
    }
  }
})
export default class Output extends Vue {
 beforeCreate(){
   this.$store.state.selected = '其他'
 }
}
